import React, {useEffect, useRef, useState} from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

import DaumPostcode from 'react-daum-postcode';

import { getProductOption} from "../../../common/utils/productOptionUtils";
import { postPayment, postOrderData, orderDataValidate } from '../services/orderService';
import { RESPONSE_MESSAGE } from '../../../common/constants/responseMessageType';
import { numberComma } from '../../../common/utils/formatNumberComma';
import { PATTERNS, REGEX } from '../../../common/constants/patterns';

import DefaultButton from '../../../common/components/DefaultButton';

import '../../../styles/order.css';

/*
     
                 state     .
            .

      kakao   API ,
      I'mport  API .
*/
function Order(){
	const location = useLocation();
    const state = location.state;

    const [orderProduct, setOrderProduct] = useState([]);
    const [totalPrice, setTotalPrice] = useState(0);
    const [orderType, setOrderType] = useState('');
    const [paymentType, setPaymentType] = useState('card');
    const [radioStatus, setRadioStatus] = useState({
        card: true,
        cash: false,
    })
    const [userAddress, setUserAddress] = useState({
        postCode: '',
        address: '',
        detail: '',
    })
    const [isOpen, setIsOpen] = useState(false);
    const [orderData, setOrderData] = useState({
        recipient: '',
        phone: '',
        orderMemo: '',
    });
    const [deliveryFee, setDeliveryFee] = useState(3500);
    const [addressOverlap, setAddressOverlap] = useState(true);
    const [recipientOverlap, setRecipientOverlap] = useState(true);
    const [phoneOverlap, setPhoneOverlap] = useState(''); // empty, inValid

    const recipientRef = useRef(null);
    const phoneRef = useRef(null);

    const navigate = useNavigate();

	useEffect(() => {
		if(state !== null) {
            setOrderProduct(state.orderProduct);
            setTotalPrice(state.totalPrice);
            setOrderType(state.orderType);

            if(state.totalPrice >= 100000)
                setDeliveryFee(0);

            const jquery = document.createElement("script");
            jquery.src = "http://code.jquery.com/jquery-1.12.4.min.js";
            const iamport = document.createElement('script');
            iamport.src = "http://cdn.iamport.kr/js/iamport.payment-1.1.7.js";
            document.head.appendChild(jquery);
            document.head.appendChild(iamport);

            return () => {
                document.head.removeChild(jquery);
                document.head.removeChild(iamport);
            }
        }else {
            navigate('/error');
        }
	}, [state]);

	//  
	const requestPay = () => {
        const { IMP } = window;
        IMP.init('');

        const price = totalPrice;
        const name = orderData.recipient;
        const phone = orderData.phone.replaceAll(REGEX.PHONE, "$1-$2-$3");
        const payAddress = `${userAddress.address} ${userAddress.detail}`;
        const postCode = userAddress.postCode;
        let orderName = orderProduct[0].productName;
        if(orderProduct.length > 1)
            orderName = `${orderProduct[0].productName}  ${orderProduct.length - 1}`;

        IMP.request_pay({
            pg: 'danal_tpay',
            pay_method: 'card',
            merchant_uid: new Date().getTime(),
            name: orderName,
            amount: price,
            buyer_email: '',
            buyer_name: name,
            buyer_tel: phone,
            buyer_addr: payAddress,
            buyer_postcode: postCode,
        }, async (res) => {
            try{
                const { data } = await postPayment(res.imp_uid);

                if(res.paid_amount === data.response.amount) {
                    alert(' ');
                    await requestOrder();
                }else {
                    alert(' ');
                }
            }catch (err) {
                alert(' ');
            }
        })
    }

	//    
	const requestOrder = async () => {
        console.log('request Order');
		try {
			const res = await postOrderData({
				orderData, 
				userAddress, 
				orderProduct, 
				deliveryFee, 
				totalPrice, 
				paymentType, 
				orderType
			});

			if(res.data.message === RESPONSE_MESSAGE.OK) {
				alert(' .');
				navigate('/');
			}
		}catch (err) {
            console.log('requestOrder error');
            console.log(err);
            console.log('errorMessage : ', err.response.data.errorMessage);
            if(err.response.status === 441 || (err.response.status === 500 && err.response.data.errorMessage === 'DBConnectionError')) {
                alert('      .\n .\n .');
            }else{
                alert(' .\n .');
            }
		}
	}

	//    
	const validateOrderData = async() => {
		try {

            console.log('orderProduct : ', orderProduct);

			const res = await orderDataValidate({
				orderProduct,
				totalPrice,
			});

			if(res.data.message === RESPONSE_MESSAGE.OK) {
				if(paymentType === 'card')
					requestPay();
				else
					await requestOrder();
			}else {
				alert('  .\n .');
				navigate('/');
			}
		}catch (err) {
			if(err.status === 440) {
				alert('  .\n .');
				navigate('/');
			}else {
                console.log('validateOrderData error');
                console.log(err);
				alert(' .\n   .');
			}
		}
	}

	//   
	//         API 
	const handleOrderSubmit = async() => {
		if(validateInputData()) 
			await validateOrderData();
	}

	//   
	const validateInputData = () => {

		if(userAddress.postCode === '') {
            setAddressOverlap(false);
			return false;
        }else if(orderData.recipient === ''){
            setRecipientOverlap(false);
            recipientRef.current.focus();
			return false;
        }else if(orderData.phone === '') {
            setPhoneOverlap('empty');
            phoneRef.current.focus();
			return false;
        }else if(!PATTERNS.PHONE.test(orderData.phone)){
            setPhoneOverlap('inValid');
            phoneRef.current.focus();
			return false;
        }

		return true;
	}

	//     
	const handlePostCodeBtn = () => {
		setIsOpen(true);
	}

	//   Radio 
	const handleRadioSelect = (e) => {
		const value = e.target.value;

		if(value === 'card') {
			setRadioStatus({
				card: true,
				cash: false,
			});

			setPaymentType('card');
		}else if(value === 'cash') {
			setRadioStatus({
				card: false,
				cash: true,
			});

			setPaymentType('cash');
		}
	}

	//DaumPostCode style
	const postCodeStyle = {
		width: '360px',
		height: '480px',
		padding: '15% 45%',
	}

	//      
	const handlePostCodeComplete = (data) => {
		const { address, zonecode } = data;
		setUserAddress({
			postCode: zonecode,
			address: address,
			detail: '',
		});

		setIsOpen(false);
		setAddressOverlap(true);
	}

	//    
	const handleClose = (state) => {
		if(state === 'FORCE_CLOSE') 
			setIsOpen(false);
		else if(state === 'COMPLETE_CLOSE')
			setIsOpen(false);
	}

	//   input  
	const handleAddressDetail = (e) => {
		setUserAddress({
			...userAddress,
			detail: e.target.value,
		});
	}

	//  input  
	const handleOrderData = (e) => {
		const { name, value } = e.target;
		setOrderData({
			...orderData,
			[name]: value,
		});

		if(name === 'recipient') 
			setRecipientOverlap(true);
		else if(name === 'phone')
			setPhoneOverlap('');
	}

	if(state === null)
		return null;
	else {
		return (
			<div className="order">
                <div className="order-header">
                    <h1> </h1>
                </div>
                <div className="order-content">
                    <div className="order-form">
                        <div className="form-content">
                            <div className="form-content-label">
                                <label></label>
                            </div>
                            <div className="form-content-input">
                                <input type={'text'} name={'recipient'} onChange={handleOrderData} value={orderData.recipient} ref={recipientRef}/>
                            </div>
                            <RecipientOverlap
                                status={recipientOverlap}
                            />
                        </div>
                        <div className="form-content">
                            <div className="form-content-label">
                                <label></label>
                            </div>
                            <div className="form-content-input">
                                <input type={'text'} name={'phone'} value={orderData.phone} onChange={handleOrderData} placeholder={'-   '} ref={phoneRef}/>
                            </div>
                            <PhoneOverlap
                                status={phoneOverlap}
                            />
                        </div>
                        <div className="form-content">
                            <div className="form-content-label">
                                <label> </label>
                            </div>
                            <div className="form-content-input-postcode">
                                <input type={'text'} name={'postCode'} placeholder={''} value={userAddress.postCode} readOnly/>
                                <DefaultButton onClick={handlePostCodeBtn} btnText={' '}/>
                            </div>
                            <div className={'form-content-input-address'}>
                                <input type={'text'} name={'address'} placeholder={''}  value={userAddress.address} readOnly/>
                            </div>
                            <div className="form-content-input-detail-address">
                                <input type={'text'} name={'detailAddress'} value={userAddress.detail} onChange={handleAddressDetail} placeholder={''}/>
                            </div>
                            <AddrOverlap
                                status={addressOverlap}
                            />
                            {isOpen && (
                                <div className={'form-postcode'}>
                                    <DaumPostcode
                                        style={postCodeStyle}
                                        onComplete={handlePostCodeComplete}
                                        onClose={handleClose}
                                    />
                                </div>
                            )}
                        </div>
                        <div className="form-content">
                            <div className="form-content-label">
                                <label> </label>
                            </div>
                            <div className="form-content-input">
                                <input type={'text'} name={'orderMemo'} value={orderData.orderMemo} onChange={handleOrderData}/>
                            </div>
                        </div>
                    </div>
                    <table className="order-table" border={1}>
                        <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                        </thead>
                        <tbody>
                        {orderProduct.map((product, index) => {
                            return (
                                <OrderTableBody
                                    key={index}
                                    data={product}
                                />
                            )
                        })}
                        </tbody>
                    </table>
                    <OrderTotalPrice
                        orderPrice={totalPrice}
                        deliveryFee={deliveryFee}
                    />
                    <div className="order-payment">
                        <div className="form-content-label">
                            <label> </label>
                        </div>
                        <div>
                            <input type={'radio'} value={'card'} checked={radioStatus.card} onChange={handleRadioSelect}/>
                            <input type={'radio'} value={'cash'} checked={radioStatus.cash} onChange={handleRadioSelect}/> 
                        </div>
                    </div>
                    <div className="order-payment-btn">
                        <DefaultButton onClick={handleOrderSubmit} btnText={''}/>
                    </div>
                </div>
            </div>
		)
	}
}


function AddrOverlap(props) {
    const { status } = props;

    if(!status){
        return (
            <OrderOverlap
                text={' '}
            />
        )
    }
}

function PhoneOverlap(props) {
    const { status } = props;

	let text = '';
	if(status === 'empty')
		text = ' ';
	else if(status === 'inValid')
		text = '  .';

	return (
		<OrderOverlap text={text} />
	)
}

function RecipientOverlap(props) {
    const { status } = props;

    if(!status){
        return (
            <OrderOverlap
                text={' '}
            />
        )
    }
}

function OrderOverlap(props) {
    const { text } = props;

    return (
        <span style={{color: "red"}}>{text}</span>
    )
}

function OrderTotalPrice(props) {
    const { orderPrice, deliveryFee } = props;

    let deliveryFeeText = '';
    let totalPrice = orderPrice;
    if(totalPrice < 100000) {
        deliveryFeeText = `${numberComma(deliveryFee)} `;
        totalPrice += deliveryFee;
    }

    return (
        <div className="order-price">
            <span className="delivery-fee"> {deliveryFeeText}</span>
            <span className="total-price">   : {numberComma(totalPrice)} </span>
        </div>
    )
}

function OrderTableBody(props) {
    const { data } = props;

    const optionText = getProductOption({size: data.size, color: data.color});

    return (
        <tr>
            <td>{data.productName}</td>
            <td>{optionText}</td>
            <td>{data.count}</td>
            <td>{numberComma(data.price)} </td>
        </tr>
    )
}


export default Order;