import React,  { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../memberSlice';

import { setToken} from "../../../common/utils/axios/tokenUtils";
import { postLogin } from '../services/memberService';
import { enhancedResponseInterceptor } from '../../../common/utils/axios/axiosInterceptors';
import { RESPONSE_MESSAGE } from '../../../common/constants/responseMessageType';

import googleOAuthBtn from '../../../assets/image/web_light_sq_ctn@1x.png';
import naverOAuthBtn from '../../../assets/image/btnG_official.png';
import kakaoOAuthBtn from '../../../assets/image/kakao_login_medium_narrow.png';

import DefaultButton from '../../../common/components/DefaultButton';

import '../../../styles/member.css';

function Login() {
	const loginStatus = useSelector(state => state.member.loginStatus);
	const [userData, setUserData] = useState({
		userId: '',
		userPw: '',
	});
	const [loginRequestStatus, setLoginRequestStatus] = useState('');
	
	const navigate = useNavigate();
	const dispatch = useDispatch();
	const { state } = useLocation();

	useEffect(() => {
		if(loginStatus) 
			navigate('/');
	}, [loginStatus]);

	const handleSubmit = (e) => {
		e.preventDefault();

		if(userData.userId === '')
			setLoginRequestStatus('id');
		else if (userData.userPw === '')
			setLoginRequestStatus('pw');
		else 
			loginSubmit();
	}

	const loginSubmit = async () => {
		try {
			const res = await postLogin(userData);
			setToken(res);
			dispatch(login(res.data));
			navigate(state);
		} catch (err) {
			console.log(err);
			console.log('login error : ', err);
			const errStatus = err.response.status;
			const errMessage = err.response.data.errorMessage;

			//BadCredentialsException     
			if(errStatus === 403 && errMessage === RESPONSE_MESSAGE.LOGIN_FAIL)
				setLoginRequestStatus('fail');
			else
				enhancedResponseInterceptor(err);
		}
	}

	// input 
	const handleChange = (e) => {
		const { name, value } = e.target;
		setUserData({
			...userData,
			[name]: value,
		})
	}

	//   
	const handleRegister = () => {
		navigate('/register');
	}

	//    
	const handleSearchId = () => {
		navigate('/search-id');
	}

	//    
	const handleSearchPw = () => {
		navigate('/search-pw');
	}

	// OAuth2   
	//href      state   
    //sessionStorage  oAuth   .
	const handleOAuth = (e) => {
        const oAuthClient = e.target.name;

        window.sessionStorage.setItem('prev', state.toString());

        if(oAuthClient === 'google')
            window.location.href='http://localhost:8080/oauth2/authorization/google';
        else if(oAuthClient === 'naver')
            window.location.href='http://localhost:8080/oauth2/authorization/naver';
        else if(oAuthClient === 'kakao')
            window.location.href='http://localhost:8080/oauth2/authorization/kakao';
    }

	return (
        <div className="content login-content">
            <div className="login-header">
                <h1></h1>
            </div>
            <div className="login-form">
                <div className="form-group">
                    <input type="text" className="form-control" name={'userId'} placeholder={''} onChange={handleChange} value={userData.userId} autoFocus={true}/>
                </div>
                <div className="form-group">
                    <input type="password" className="form-control" name={'userPw'} placeholder={''} onChange={handleChange} value={userData.userPw}/>
                </div>
                <LoginOverlap
                    status={loginRequestStatus}
                />
                <div className="login-form-btn-area">
                    <div className="login-btn">
                        <DefaultButton className={'login-btn'} onClick={handleSubmit} btnText={'Login'}/>
                    </div>
                    <div className="join-search-area">
                        <DefaultButton className={'join-btn'} onClick={handleRegister} btnText={''}/>
                        <DefaultButton className={'search-id-btn'} onClick={handleSearchId} btnText={' '}/>
                        <DefaultButton className={'search-pw-btn'} onClick={handleSearchPw} btnText={' '}/>
                    </div>
                    <div className="oauth-area">
                        <div className="oauth-btn">
                            <div className="oauth-btn">
                                <label htmlFor={'oauth-google'}>
                                    <img src={googleOAuthBtn} alt={' '}/>
                                </label>
                            </div>
                            <div className="oauth-btn">
                            <label htmlFor={'oauth-naver'}>
                                <img src={naverOAuthBtn} alt={' '}/>
                            </label>
                            </div>
                            <div className="oauth-btn">
                                <label htmlFor={'oauth-kakao'}>
                                    <img src={kakaoOAuthBtn} alt={' '}/>
                                </label>
                            </div>
                        </div>
                        <button id={'oauth-google'} name={'google'} onClick={handleOAuth} style={{display: 'none'}}/>
                        <button id={'oauth-naver'} name={'naver'} onClick={handleOAuth} style={{display: 'none'}}/>
                        <button id={'oauth-kakao'} name={'kakao'} onClick={handleOAuth} style={{display: 'none'}}/>
                    </div>
                </div>
            </div>
        </div>
    )
}

function LoginOverlap({ status }) {
	let text = '';

	if(status === 'id')
		text = ' .';
	else if(status === 'pw')
		text = ' .';
	else if(status === 'fail')
		text = '    .';
	
	return (
		<div className="login-overlap">
			<span>{text}</span>
		</div>
	)
}

export default Login;