import React, { useEffect, useState } from "react";

import { getNewProductList } from "../services/mainService";

import MainContent from "../components/MainContent";

/*
      .
       
 */
function New() {
	const [data, setData] = useState([]);

	useEffect(() => {
		const getNew = async () => {
			try {
				const res = await getNewProductList();
				setData(res.data);
			} catch (err) {
				console.log(err);
			}
		}

		getNew();
	}, []);

	return (
		<>
			<MainContent
				data={data}
				classification={'NEW'}
			/>
		</>
	)
}

export default New;